import userReducer from './user';
import vacationsReducer from './vacations';
import errorReducer from './errorHandler';
import likeReducer from './likes';

const initialState = {
    user: {
        _id: 0,
        username: '',
        firstname: 'guest',
        lastname: '',
        permition: 0
    },
    vacations: [
        {
            _id: 0,
            title: '',
            description: '',
            image: ''
        }
    ],
    error: {
        hasError: false,
        message: ''
    },
    likes: []
}

const Reducer = (state = initialState, action) => {
    switch (action.type) {
        case 'RESET_STATE':
            return initialState
        default:
            return {
                user: userReducer(state, action),
                vacations: vacationsReducer(state, action),
                error: errorReducer(state, action),
                likes: likeReducer(state, action)
            }
    }
}

export default Reducer;