import React, { Component } from 'react';
import { connect } from 'react-redux';
import './pages-styles/pageStyle.css';

import ServiceHoc from '../components/hoc/service-hoc';
import Login from '../components/login/login';
import SignUp from '../components/sign-up/signUp';

class HomePage extends Component {

    constructor(props) {
        super(props);
        this.state = {
            isLoggedIn: false,
            isAdmin: false
        }
    }

    componentDidUpdate(prevProps) {
        const { history: { push }} = this.props;
        if (prevProps.user._id !== this.props.user._id || 0) {
            if (this.props.user.permition === 1) {
                return push('/editor')
            } else {
                return push('/vacations')
            }
        }
    }
    
    render() {
        return <div className="home-page">
            <h1>see your next vacation!!!</h1>
            { this.props.match.path === '/sign-up' ? <SignUp {...this.props}/> : <Login {...this.props}/> }
        </div>
    }
}

const mapStateToProps = state => {
    return {
        user: state.user
    }
}

export default ServiceHoc()(connect(mapStateToProps, null)(HomePage));