export const test = (user) => {
    return {
        type: 'TEST',
        payload: user
    }
}

export const userData = (user) => {
    return {
        type: 'USER_DATA',
        payload: user
    }
}

export const userLikes = (data) => {
    return {
        type: 'GET_LIKES',
        payload: data
    }
}