import React, { Component } from 'react';
import { connect } from 'react-redux';
import { getVacations } from '../actions/vacation.Action';
import { gotError } from '../actions/errorActions';
import './pages-styles/pageStyle.css';

import ServiceHoc from '../components/hoc/service-hoc';
import Navbar from '../components/navbar/navbar';
import VacationEditor from '../components/vacation-editor/vacationEditor';
import VacationCard from '../components/vacation-card/vacationCard';

class AdminPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            vacationID: 0,
            editor: false
        }
    }

    errorHandler(data) {
        this.props.gotError(data);
        this.props.history.push('/');
    }

    getAllVacations() {
        const { service: {getAllVacations}, getVacations } = this.props;
        getAllVacations().then(({data}) => getVacations(data))
        .catch(({response: {data}}) => this.errorHandler(data));
    }

    getVacationDetailsForChange() {
        const { vacations } = this.props;
        const { vacationID } = this.state;
        return vacationID ? vacations[vacationID] : null;
    }

    getVacationDetails(i=null) {
        this.setState({
            editor: true,
            vacationID: i,
        });
    }

    closePage() {
        this.getAllVacations();
        this.setState({
            editor: false
        });
    }

    deleteVac(i) {
        const id = this.props.vacations[i]._id;
        this.props.service.deleteVacation(id).then(() => {
            this.getAllVacations();
        }).catch(({response: {data}}) => this.errorHandler(data));
    }

    render() {
        const { user: {permition}, likes, vacations, service } = this.props;
        const {editor} = this.state;
        return <div className="admin-page">
            {editor ? <VacationEditor service={service} closePage={() => this.closePage()} vacationDetails={this.getVacationDetailsForChange()}/> : null}
            <Navbar {...this.props} chooseVacationID={() => this.getVacationDetails()}/>
            <div className="vacations">
                {vacations.map((vac, idx) => <VacationCard vacation={vac} key={idx} permition={permition}
                likes={likes} imgUrl={service.getImage(vac.image)}
                chooseVacationID={() => this.getVacationDetails(idx)}
                secondFunction={() => this.deleteVac(idx)}/>)}
            </div>
        </div>
    }

    componentDidMount() {
        this.getAllVacations();
    }
}

const mapStateToProps = state => {
    return state;
}

const mapDispatchToProps = dispatch => {
    return {
        getVacations: (vacs) => dispatch(getVacations(vacs)),
        gotError: (data) => dispatch(gotError({
            hasError: true, 
            message: data.message
        }))
    }
}

export default ServiceHoc()(connect(mapStateToProps, mapDispatchToProps)(AdminPage));