import React, { Component, Fragment } from 'react';
import { connect } from 'react-redux';
import './navbar.css';

import { userData } from '../../actions/userActions';
import { logout } from '../../actions/logoutAction';
import { gotError } from '../../actions/errorActions';

class Navbar extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isAdmin: false
        }
    }

    logoutBtn() {
        this.props.service.logout().then(() => {
            this.props.nullUser();
            this.props.history.push('/');
        })
    }

    chartBtn() {
        this.props.history.push('/charts');
    }

    render() {
        const { firstname, lastname } = this.props.user;
        return <nav>
            <div className="nav-text">
                <h1>Vacations</h1>
                <span>hello {firstname} {lastname}</span>
            </div>
            <div className="nav-buttons">
                { this.state.isAdmin ? this.adminButtons() : null }
                <button className="logout-btn" onClick={() => this.logoutBtn()}>
                    <i className="material-icons">power_settings_new</i>
                </button>
            </div>
        </nav>
    }

    adminButtons() {
        return <Fragment>
            <button className="add-btn" onClick={() => this.props.chooseVacationID()}>
                <i className="material-icons">add</i>
            </button>
            <button className="charts-btn" onClick={() => this.chartBtn()}>
                <i className="material-icons">bar_chart</i>
            </button>
        </Fragment>
    }

    componentDidMount() {
        const { user, service, loggedIn, history } = this.props;
        if (user.firstname === 'guest' && user._id === 0) {
            service.loggedIn().then(({data}) => loggedIn(data))
            .catch(({response: {data}}) => {
                this.gotError(data);
                history.push('/');
            });
        }
        if (this.props.user.permition > 0) {
            this.setState({
                isAdmin: true
            })
        }
    }
}

const mapStateToProps = (state) => {
    return {
        user: state.user
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        loggedIn: (user) => dispatch(userData(user)),
        nullUser: () => dispatch(logout()),
        gotError: (data) => dispatch(gotError({
            hasError: true, 
            message: data.message
        }))
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(Navbar);