import React, { Component } from 'react';
import './vacationEditor.css';

class VacationEditor extends Component {
    constructor(props) {
        super(props);
        this.state = {
            _id: 0,
            title: '',
            description: '',
            image: ''
        }
    }

    onChange(field, value) {
        this.setState({
            [field]: value
        });
    }

    sendVac() {
        this.props.service.sendVacation(this.state).then(() => {
            this.props.closePage();
        }).catch((err) => console.error(err));
    }

    render() {
        const { title, description } = this.state;
        return <div className="editor-page">
            <h2>vacation editor</h2>
            <div className="vacation-editor">
                <input type="text" placeholder="Title" value={title} onChange={({target}) => this.onChange('title', target.value) }/>
                <textarea placeholder="Description" value={description} onChange={({target}) => this.onChange('description', target.value) }></textarea>
                <label className="editor-file" htmlFor="file-upload">Choose your file</label>
                <input id="file-upload" type="file" onChange={({target}) => this.onChange('image', target.files[0]) }></input>
                <div className="btns">
                    <button className="submit-btn" onClick={() => this.sendVac()}>submit</button>
                    <button className="close-btn" onClick={() => this.props.closePage()}>close</button>
                </div>
            </div>
        </div>
    }

    componentDidMount() {
        if (this.props.vacationDetails === null) {
            return null
        }
        const {_id, title, description, image} = this.props.vacationDetails;
        this.setState({
            _id,
            title,
            description,
            image
        });
    }
}

export default VacationEditor;