import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { connect } from 'react-redux';
import './signUp.css';

import { userData } from '../../actions/userActions';
import { gotError } from '../../actions/errorActions';

class SignUp extends Component {

    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: '',
            firstname: '',
            lastname: ''
        }
    }

    componentDidUpdate() {
        const { error, expiredError } = this.props;
        if (error.hasError === true) {
            setTimeout(() => expiredError(), 4000);
        }
    }

    render() {
        const { error } = this.props;
        const { username, firstname, lastname, password } = this.state;
        return <div className="log-template">
            <h2>Sign Up</h2>
            <div className="form">
                <input type="text" value={firstname} onChange={({target}) => this.setState({firstname: target.value})}
                placeholder="firstname" required></input>
                <input type="text" value={lastname} onChange={({target}) => this.setState({lastname: target.value})}
                placeholder="lastname" required></input>
                <input type="text" value={username} onChange={({target}) => this.setState({username: target.value})}
                placeholder="username" required></input>
                <input type="password" value={password} onChange={({target}) => this.setState({password: target.value})}
                onKeyDown={(event) => event.key === 'Enter' ? this.props.registration(this.state) : null }
                placeholder="password" required></input>
                { error.hasError ? <p className="error-text">{error.message}</p> : null}
                <button onClick={() => this.props.registration(this.state)}>Submit</button>
                <p>back to <Link to="/login">Login</Link></p>
            </div>
        </div>
    }
}

const mapDispatchToProps = (dispatch, ownProps) => {
    const { service: { signUp } } = ownProps;
    return {
        registration: (user) => signUp(user).then(({data}) => dispatch(userData(data)))
        .catch(({response: {data}}) => dispatch(gotError({
            hasError: true, 
            message: data.message
        }))),
        expiredError: () => dispatch(gotError({
            hasError: false, 
            message: ''
        }))
    }
}

const mapStateToProps = state => {
    return state;
}

export default connect(mapStateToProps, mapDispatchToProps)(SignUp);