import React from 'react';
import { ConsumerVacation } from '../../service/context';

const ServiceHoc = () => (Wrapped) => {
    return (props) => {
        return <ConsumerVacation>
            {
                (vacationService) => <Wrapped {...props} service={vacationService} />
            }
        </ConsumerVacation>
    }
}

export default ServiceHoc;