import React from 'react';
import './vacation-full.css';

const VacationDescription = (props) => {
    const { vacation, closeDetailPage, imgUrl } = props;
    return <div className="vacation-full-page" onClick={() => closeDetailPage()}>
        <div className="vacation-description">
            <div className="title-fullPage">
                <p>{ vacation.title }</p>
                <button onClick={() => closeDetailPage()}>
                    <i className="material-icons">close</i>
                </button>
            </div>
            <img src={imgUrl(vacation.image)} alt="vacation_pic"/>
            <div className="full-description">
                <p>{ vacation.description }</p>
            </div>
        </div>
    </div>
}

export default VacationDescription;