import React, { Component } from 'react';
import { connect } from 'react-redux';
import { userLikes } from '../actions/userActions';
import { getVacations } from '../actions/vacation.Action';
import { gotError } from '../actions/errorActions';
import './pages-styles/pageStyle.css';

import ServiceHoc from '../components/hoc/service-hoc';
import Navbar from '../components/navbar/navbar';
import VacationCard from '../components/vacation-card/vacationCard';
import VacationDescription from '../components/vacation-full-page/vacation-full';

class VacationsPage extends Component {
    constructor(props) {
        super(props);
        this.state = {
            showVacationDetails: false,
            vacationID: ''
        }
    }

    getAllVacations() {
        const { service: {getAllVacations}, getVacations } = this.props;
        getAllVacations().then(({data}) => {getVacations(data)})
        .catch(({response: {data}}) => {
            this.props.gotError(data);
            this.props.history.push('/');
        });
        this.getUserLikes(this.props.user._id);
    }

    getUserLikes(userId) {
        this.props.service.usersWatch(userId).then(({data}) => {
            this.props.userLikes(data);
        });
    }

    getVacationDetails(i) {
        this.setState({
            showVacationDetails: true,
            vacationID: i
        });
    }

    closeDetailPage() {
        this.setState({
            showVacationDetails: false
        });
    }

    usersLikeFunc(vacationId) {
        const { user, likes, service: { userWatchFunc } } = this.props;
        const like = likes.find(({vacId}) => vacationId === vacId);
        userWatchFunc(user._id, vacationId, like).then(() => this.getUserLikes(user._id))
        .catch((err) => console.log('likes error', err));
    }

    render() {
        const { vacations, user: {permition}, likes, service: { getImage } } = this.props;
        const { showVacationDetails, vacationID } = this.state;
        if (vacations === null | undefined) {
            return null;
        }
        return <div className="vacations-page">
            {showVacationDetails ? <VacationDescription vacation={vacations[vacationID]}
            imgUrl={(img) => getImage(img)}
            closeDetailPage={() => this.closeDetailPage()}/> : null}
            <Navbar {...this.props}/>
            <div className="vacations">
                {vacations.map((vac, idx) => <VacationCard vacation={vac} key={idx} permition={permition}
                likes={likes}
                imgUrl={getImage(vac.image)}
                chooseVacationID={() => this.getVacationDetails(idx)}
                secondFunction={() => this.usersLikeFunc(vac._id)}/>)}
            </div>
        </div>
    }

    componentDidMount() {
        this.getAllVacations();
    }
}

const mapStateToProps = state => {
    return state;
}

const mapDispatchToProps = dispatch => {
    return {
        getVacations: (vacs) => dispatch(getVacations(vacs)),
        userLikes: (likes) => dispatch(userLikes(likes)),
        gotError: (data) => dispatch(gotError({
            hasError: true, 
            message: data.message
        }))
    }
}

export default ServiceHoc()(connect(mapStateToProps, mapDispatchToProps)(VacationsPage));