import React, { Fragment } from 'react';
import './vacationCard.css';

const VacationCard = (props) => {
    const { vacation, permition, imgUrl } = props;
    return <div className="card">
        <img src={imgUrl} alt="vacation_pic"/>
        <div className="card-title">
        <span>{ vacation.title }</span>
        </div>
        <div className="card-btn">
            { permition === 1 ? adminBtn(props) : userBtn(props) }
        </div>
    </div>
}

const adminBtn = (props) => {
    return <Fragment>
        <button onClick={() => props.chooseVacationID()}>
            <i className="material-icons">edit</i>
        </button>
        <button className="like-btn" onClick={() => props.secondFunction()}>
            <i className="material-icons">delete</i>
        </button>
    </Fragment>
}

const userBtn = (props) => {
    const like = props.likes.find(({vacId}) => vacId === props.vacation._id);
    return <Fragment>
        <button onClick={() => props.chooseVacationID()}>
            <i className="material-icons">info</i>
        </button>
        <button className={!!like ? "like-btn liked": "like-btn"} onClick={() => props.secondFunction()}>
            <i className="material-icons">favorite_border</i>
        </button>
    </Fragment>
}

export default VacationCard;