import axios from 'axios';

const PATH = 'https://dima-vacs.herokuapp.com';
// const PATH = 'http://localhost:3500';

export default class VacationService {
    loggedIn() {
        return axios.post(`${PATH}/user/loggedIn`);
    }
    
    login(user) {
        user.username = user.username.toLowerCase();
        return axios.post(`${PATH}/user/login`, user);
    }

    signUp(user) {
        return axios.put(`${PATH}/user/sign-up`, user);
    }

    logout() {
        return axios.get(`${PATH}/user/logout`);
    }

    getAllVacations() {
        return axios.get(`${PATH}/vacation`);
    }

    getImage(imgId) {
        return `${PATH}/vacation/vacImg/${imgId}`;
    }

    sendVacation(vacationData) {
        console.log('sendVacation', vacationData);
        console.log(typeof vacationData.image)
        const vacData = new FormData();
        const config = {'content-type': 'multipart/form-data'}
        if (vacationData.title !== '' || vacationData.description !== '') {
            if (typeof vacationData.image === 'object') {
                console.log('file added')
                vacData.append("image", vacationData.image);
            }
            vacData.append('title', vacationData.title);
            vacData.append('description', vacationData.description);
        }
        return vacationData._id === 0 ?  axios.put(`${PATH}/vacation`, vacData, {headers: config}) : 
        axios.patch(`${PATH}/vacation/${vacationData._id}`, vacData, {headers: config});
    }

    deleteVacation(vacId) {
        return axios.delete(`${PATH}/vacation/${vacId}`);
    }

    usersWatch(userId) {
        return axios.get(`${PATH}/likes/${userId}`);
    }

    userWatchFunc(userId, vacId, like) {
        return !!like ? axios.delete(`${PATH}/likes/${like.userId}/${like.vacId}`) : axios.put(`${PATH}/likes/${userId}/${vacId}`);
    }
}